
.card {
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
    margin: 30px 0 30px 0;
    border: none;
    background-color: #F8F8F9;

    .card-header {
        background-color: var(--pink);
        padding: 50px 50px 0 50px;

        p {
            color: white;
        }

        .d-flex {
            .headerImage {
                border: 1px solid white;
                border-radius: 50%;

                img {
                    width: 80px;
                    height: 80px;
                    border-radius: 50%;
                }
            }

            h1 {
                color: white;
                align-items: center;
                display: flex;
            }
        }

        .salaryInfo {
            display: grid;

            p {
                margin-bottom: 10px;
                width: fit-content;
                background: #f2effe56;
                border-radius: 8px;
                padding: 4px 10px;
                font-weight: 500;
                font-size: 12px;
                // line-height: 18px;
            } 
        }

    }

    .apply {
        justify-content: flex-end;
        display: flex;
        top: -20px;
        position: relative;
        margin-right: 60px;

        a,button {
            background-color: rgb(93, 56, 255);
            border: 1.5px solid #F8F8F9;
            color: white;
        }:hover{
            background-color: rgb(114, 82, 255);
            border: 1.5px solid #F8F8F9;
            color: white;
        }

        :focus {
            outline: none
        }
    }

    .card-body {
        .top_infos {
            p {
                margin-right: 20px;
                margin-left: 20px;
                color: #525252;
                display: list-item;
            }
        }

        .body_infos {
            .works {

                p {
                    display: list-item;
                    color: #525252;
                    width: 75%;
                }
            }

            .skills {
                p {
                    display: list-item;
                    margin-left: 20px;
                    width: 75%;
                }
            }
        }
    }

    .card-footer {
        background-color: #F8F8F9;

        h6 {
            top: 5px;
            margin-right: 30px;
            position: relative;
        }
    }
}