* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.Title h1 {
  font-size: 68px;
  font-weight: 700;
  line-height: 78px;
  color: black;
}

.infos {
  margin-top: 50px;
}
.infos .add {
  margin-top: 20px;
  margin-bottom: 20px;
}
.infos .add h3 {
  font-size: 28px;
  line-height: 48px;
  color: #2C2C2C;
  font-weight: 400;
}
.infos .add a {
  cursor: pointer;
  text-decoration: underline;
  position: relative;
  font-size: 48px;
  line-height: 48px;
  font-weight: 500;
  color: #785BF4;
  opacity: 0.9;
}
.infos .add a:hover {
  cursor: pointer;
  text-decoration: underline;
  position: relative;
  font-size: 48px;
  line-height: 48px;
  font-weight: 500;
  color: #785BF4;
  opacity: 1;
}

.submitBut {
  font-size: 18px;
  transition: all 0.5s;
  opacity: 0.8;
}

.submitBut:hover {
  opacity: 1;
}

.mail {
  color: #785BF4;
  font-weight: 500;
  font-size: 26px;
  text-decoration: underline;
}

.form-control:focus {
  color: #212529;
  background-color: #fff;
  border-color: #785BF4 !important;
  outline: none;
  box-shadow: 0 0 0 0.25remrgba 13, 110, 253, 0.25;
}/*# sourceMappingURL=Contact.css.map */